*{
    box-sizing: border-box;
}
body{
    margin: 0;
    padding: 0;
    background-color: rgb(35, 36, 35);
    background-image: url("./fifa.jpg");
    background-size:cover ;
    background-blend-mode:multiply;
}

.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.search-input{
    display: flex;
    justify-content: flex-end;
    width: 100%;

}

#input{
    width: 100%;
    padding: 0.5em;
    font-size: 1.5em;
    outline: none;
}

.search-icon{
    padding: 0.5em 1.5em;
    display: flex;
    justify-content: center;
    background-color: red;
    color: #fff;
    font-weight: bold;
    font-size: 1.5em;
}

#close-btn{
    color: #fff;
    cursor: pointer;
}
.fifa-title{
    font-size: 6em;
    margin: 1em 0;
}
.display-search-data{
    display: flex;
    flex-direction: column;
    text-align: center;
    align-content: space-between;
    margin: 1em 0;
}
.playes-container{
    display: flex;
    justify-content: center;
}
.playes{
    width: 100%;
    padding: 1em 1.5em;
    margin: 1em;
}

.playes h1,
.playes p {
    margin: 0;
}

.playes p{
    margin-top: 1em;
    font-size: 1em;
    font-weight: bold;
}


.player-info{
    display: flex;
    justify-content: space-between;
}


  
  header ul {
    display: flex;
    align-items: center;
    margin-top: 0;
    padding-left: 10px;
  }
  
  header ul li {
    margin-right: 25px;
    list-style: none;
  }
  
  header li a {
    text-decoration: none;
    font-size: 20px;
  }
  
  header li a:visited {
    color: #dcdce2;
  }
h1{
    margin: 0;
}
  h1{
      text-align: center;
      color: #dcdce2;
      font-weight: bold;
      font-size: 3.5em;
  }
.player-details-container{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.grapt-image{
    width: 50%;
    background-color: rgb(9, 38, 172);
    height: 300px;
    text-align: center;
    color: #fff;
}
.details-container{
    margin-top: 1em;
}
.player-details{
    display: flex;
    justify-content: space-between;
    margin-bottom: 1em;
}

.player-details button{
    padding: 0.5em 1em;
    font-size: 1em;
    font-weight: bold;
    border:none;
    margin: 0.5em;
    background-color: darkblue;
    border-radius: 5px;
    color: #dcdce2;
}
.details{
    margin-bottom: 1em;
}
.details p{
    margin: 0;
    color: #dcdce2;
    font-size: 1em;
}
.details span{
    margin-left: 1em;
    color: #a7a7ad;
    font-size: 1em;
    font-weight: bold;
}

@media(min-width: 700px){
    body{
        box-sizing: border-box;
        color: chartreuse;
    }
    .app{
        width: 80%;
        max-width: 1800px;
        margin: 0 auto;
    }
    #input{
        width: 50%;
    }
    .fifa-title{
        text-align: right;
        margin-right: 1em;
    }
    .display-search-data{
        flex-direction: row;
        justify-content: space-around;
        flex-wrap: wrap;
    }
    .playes{
        width: 300px;
        margin: 0 auto;

    }
    .player-details-container{
        flex-direction: row;
        justify-content: space-around;
    }

    .details{
        border-left: 1px solid #dcdce2;
        padding-left:2em;
    }
}
